import Api from '@/common/api';
export default {
	data() {
		return {
			newPassword: "",
			confimpassword: "",
			phone: "",
			logining: false,
			isCodeDisable: false,
			btnText: "获取验证码",
			timer: null,
			code: "",
			countdown: 30,
		};
	},
	methods: {
		navBack(){
			uni.navigateTo({
				url:'/pages/public/login'
			})
		},
		async clickSendCode() {
			if (!/^1[1-9][0-9]\d{8}$/.test(this.phone)) {
				this.$api.msg("手机号码格式有误");
				return;
			}
			const data = await Api.apiCall("POST", Api.member.geneCode, {
				phone: this.phone
			});
			if (data.code == 0) {
				this.isCodeDisable = true;
				this.timer = setInterval(() => {
					this.btnText = "倒计时" + (--this.countdown);
					if (this.countdown <= 0) {
						this.countdown=30;
						this.isCodeDisable = false;
						clearInterval(this.timer);
						this.btnText="获取验证码";
					}
				}, 1000);
				return;
			}
			uni.showToast({
				title: data.msg
			})
		},
		validateForm() {
			try {
				if (!this.phone) {
					throw '请填写手机号';
				}
				let phoneReg = /^1[1-9][0-9]\d{8}$/;
				if (!phoneReg.test(this.phone)) {
					throw '手机号格式有误';
				}
				if (!this.newPassword) {
					throw '请填写密码';
				}
				if (this.newPassword != this.confimpassword) {
					throw "密码不一致";
				}
				return true;
			} catch (err) {
				this.$api.msg(err);
				return false;
			}
		},
		async update() {
			if (!this.validateForm()) return;
			let params = {
				newPassword: this.newPassword,
				phone: this.phone,
				code: this.code
			};
			const data = await Api.apiCall('POST', Api.member.forgetPwd, params);
			if (data.code == 0) {
				this.$api.msg("修改成功");
				setTimeout(() => {
					uni.navigateTo({
						url: '/pages/public/login'
					})
				}, 1000);

				return true;
			}
			this.$api.msg(data.msg);
		}
	}
}
